<template>
  <button
    class="btn"
    style=" width: 90px"
    :title="rune.option"
    data-bs-toggle="tooltip"
  >
    <img :src="require(`@/assets/diablo2/images/rune/${rune.name}.png`)" />
    <br />
    {{ rune.nameKo }}
  </button>
</template>

<script>
import runeList from "@/assets/diablo2/data/rune.json";

export default {
  props: { id: Number },
  data() {
    return {
      runeList,
    };
  },
  setup(props) {
    // rune set
    var targetRune = null;
    for (var i = 0; i < runeList.length; i++) {
      if (runeList[i].id === props.id) {
        targetRune = runeList[i];
        break;
      }
    }

    return { rune: targetRune };
  },
  methods: {},
};
</script>

<style></style>
